.thumbnail-container {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 100; /* Adjust the z-index to ensure the thumbnail appears above other content */
}

.thumbnail {
  width: 100px; /* Adjust the width as needed */
  height: 100px; /* Adjust the height as needed */
  /* Additional styling for your thumbnail */
  border-radius: 50%;
}

.content {
  display: flex;
  height: 100vh;
}

.child {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: end;
}

@media (max-width: 1024px) {
  .content {
    flex-direction: column;
  }

  .child:nth-child(1) {
    order: 2;
  }

  .child:nth-child(2) {
    order: 1;
  }

  .child:nth-child(3) {
    order: 3;
  }
}

@media (max-width: 768px) {
  .content {
    flex-direction: column;
  }

  .child:nth-child(1) {
    order: 2;
  }

  .child:nth-child(2) {
    display: none;
  }

  .child:nth-child(3) {
    order: 3;
  }
}